import enUsIntl from "antd/es/locale/en_US";
import MobileeEnUSIntl from "antd-mobile/es/locales/en-US";
import { ConfigProvider } from "antd";
import { ConfigProvider as MobileConfigProvider, SafeArea } from "antd-mobile";
import { BrowserRouter, Route, Routes, Navigate } from "react-router-dom";
import ScrollToTop from "@Components/ScrollToTop/ScrollToTop";
import Loading from "@Components/Loading/Loading";
import "./App.less";
import React, { Suspense } from "react";

const HomePage = React.lazy(() => import("@Pages/Home/Home"));
const ProductDetailPage = React.lazy(() =>
  import("@Pages/ProductDetail/ProductDetail")
);
const ShoppingCart = React.lazy(() =>
  import("@Pages/ShoppingCart/ShoppingCart")
);
const ShippingMethod = React.lazy(() =>
  import("@Pages/ShippingMethod/ShippingMethod")
);
const ShippinngPayment = React.lazy(() =>
  import("@Pages/ShippingPayment/ShippingPayment")
);

const App = () => {
 
  return (
    <>
      <div style={{ width: "100%", height: "100%" }}>
        <MobileConfigProvider locale={MobileeEnUSIntl}>
          <ConfigProvider locale={enUsIntl}>
            <div style={{ background: "#ffffff" }}>
              <SafeArea position="top" />
            </div>
            <div
              style={{
                position: "relative",
                height:
                  "calc( 100% - env(safe-area-inset-bottom) - env(safe-area-inset-top) )",
                overflow: "hidden",
              }}
            >
              <Suspense fallback={<Loading />}>
                <BrowserRouter>
                  <ScrollToTop />
                  <Routes>
                    <Route path="/" element={<HomePage />} />
                    <Route
                      path="/product/:productId"
                      element={<ProductDetailPage />}
                    />
                    <Route path="/cart" element={<ShoppingCart />} />
                    <Route path="/cart/shipping" element={<ShippingMethod />} />
                    <Route
                      path="/cart/payment"
                      element={<ShippinngPayment />}
                    />
                    <Route path="*" element={<Navigate replace to="/" />} />
                    <Route path="/loading" element={<Loading />} />
                  </Routes>
                </BrowserRouter>
              </Suspense>
            </div>
            <div style={{ background: "#ffffff" }}>
              <SafeArea position="bottom" />
            </div>
          </ConfigProvider>
        </MobileConfigProvider>
      </div>
    </>
  );
};

export default App;
